import Vue from 'vue'
import VueRouter from 'vue-router'
import Main2utor from '../views/Main2utor.vue'
import CartPage from '../views/CartPage.vue'
import Tutor from '../views/Tutor.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main-2utor',
    component: Main2utor
  },
  {
    path: '/cart',
    name: 'cart-page',
    component: CartPage
  },
  {
    path: '/tutor/:id',
    name: 'tutor',
    component: Tutor,
    props: true
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
