<template>
  <div>
    <div id="s-bodyContainer">
      <div class="row">
        <MainTitle></MainTitle>
      </div>
    </div>
    <div class="search-parent ml-2 mb-3">
      <div class="search-bar">
        <form>
          <input
            v-model="searchField"
            class="form-control"
            type="text"
            placeholder="Search by subject name"
            aria-label="Search"
          />
        </form>
      </div>
      <ShoppingCart :cart="$store.state.cart"></ShoppingCart>
    </div>

    <div container-fluid>
      <div class="row m-auto">
        <div
          v-for="tutor in subjectFilter"
          :key="tutor.id"
          class="col-md-6 card-inner"
        >
          <TutorCard
            :tutor="tutor"
            :tutorref="tutor.id"
            @add-to-cart="updateStoreCart(tutor)"
          ></TutorCard>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TutorCard from '../components/TutorCard.vue'
import MainTitle from '../components/MainTitle.vue'
import ShoppingCart from '../components/ShoppingCart.vue'

export default {
  components: {
    TutorCard,
    MainTitle,
    ShoppingCart
  },
  data() {
    return {
      cart: [],
      searchField: '',
      tutors: []
    }
  },
  methods: {
    updateCart(tutor) {
      this.cart.push(tutor)
    },
    updateStoreCart(tutor) {
      this.$store.state.cart.push(tutor)
    }
  },
  computed: {
    tutorFilter() {
      let tutors = this.tutors
      let searchField = this.searchField.toLowerCase().trim()
      if (!searchField) return this.tutors
      return tutors.filter(c => c.name.toLowerCase().indexOf(searchField) > -1)
    },
    subjectFilter() {
      let searchField = this.searchField.toLowerCase().trim()
      if (!searchField) return this.$store.state.tutors
      let tutors = this.$store.state.tutors.filter(e =>
        e.subjects.some(
          sub => sub.subject.toLowerCase().indexOf(searchField) > -1
        )
      )
      return tutors
    },
    tutorama() {
      return this.$store.state.tutors
    }
  }
}
</script>

<style scoped>
#s-bodyContainer {
  width: 100%;
}
#s-saleItems {
  background-color: rgba(118, 53, 131, 0.1);
  margin: auto;
}
.middle {
  margin: auto;
  width: 50%;
  border: 3px solid green;
  padding: 10px;
}

.search-parent {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.search-bar {
  position: relative;
}

.search-bar input {
  padding-left: 30px;
}

.card-inner {
  position: relative;
  overflow: hidden;
}
</style>
