<template>
  <div>
    <h1>Your Shopping Cart:</h1>
    <div v-for="(item, index) in cart" :key="index">
      <img :src="item.image" :alt="item.alt" />
      <h2>
        {{ item.name }} ${{ item.price }} ph
        <button class="btn btn-danger ml-5" @click="remove(index)">
          <i class="far fa-times-circle" style="color: white"></i>
        </button>
      </h2>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {}
  },
  computed: {
    cart() {
      return this.$store.state.cart
    }
  },
  methods: {
    remove(id) {
      this.$store.state.cart.splice(id, 1)
    }
  }
}
</script>

<style scoped>
img {
  width: 200px;
}
</style>
