<template>
  <div
    class="card mb-3 rounded-lg"
    id="s-saleItemsCol"
    style="max-width: 600px;"
  >
    <div class="row no-gutters">
      <div class="col-md-4">
        <img v-bind:src="tutor.image" class="card-img" :alt="tutor.alt" />
      </div>
      <div class="col-md-8">
        <div class="card-body">
          <!--<router-link
            class="routes"
            :to="{ name: 'tutor', params: { id: tutarama.id } }"
          >-->
          <h5 id="s-name" class="card-title">{{ tutor.name }}</h5>
          <!--</router-link>-->
          <p
            v-for="subject in tutor.subjects"
            :key="subject.id"
            id="s-fields"
            class="card-text"
          >
            {{ subject.subject }} <br />
          </p>
          <p id="s-rating" class="card-text">
            {{ tutor.rating }}
            <i class="fas fa-star" style="color: #CACC27"></i>
            ${{ tutor.price }} p.h.
            <button class="btn btn-success ml-5" @click="addToCart">
              <i class="far fa-plus-square" style="color: #2B7B68"></i>
            </button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  //props: ['id', 'name', 'rating', 'price', 'subjects', 'alt', 'image'],
  props: { tutor: Object, tutorref: Number },
  methods: {
    addToCart() {
      this.$emit('add-to-cart')
    }
  },
  computed: {
    tutarama() {
      return this.$store.state.tutors[this.tutorref]
    }
  }
}
</script>

